import { useEffect } from "react";

const InitHeader = () => {
 
    useEffect(() => {


        fetch('https://api.github.com/repos/xizon/funda-ui/releases/latest')
            .then(response => {
                return response.json();
            })
            .then(res => {

                const ver = res.tag_name.toLowerCase().trim();
                const verContainer = document.querySelectorAll(".app-version");
                verContainer.forEach((el: any) => {
                    el.innerHTML = ver;
                });
                   
            })
            .catch(err => console.log('Request Failed', err));

    }, []);
    return null;
}

export default InitHeader;
